import { Component, Input } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { ValueAccessor } from '@helix/platform/shared/components';
import { OpenViewActionModalSize } from '@helix/platform/view/api';
import { cloneDeep, noop } from 'lodash';
import { RecordGridCellDisplayPropertiesEditorComponent } from './cell-display-properties-editor.component';
import {
  ICellDisplayProperties,
  ICellDisplayPropertiesEditorConfig
} from '../../../../../common/types/cell-display-properties.types';
import { IColumnEditorColumnData } from '../../record-grid-column-editor.types';
import { RxModalService } from '@helix/platform/ui-kit';

@Component({
  selector: 'rx-cell-display-properties',
  templateUrl: './cell-display-properties.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: RecordGridCellDisplayPropertiesComponent,
      multi: true
    }
  ]
})
export class RecordGridCellDisplayPropertiesComponent extends ValueAccessor<ICellDisplayProperties[]> {
  @Input()
  columnName: string;

  @Input()
  columns: IColumnEditorColumnData[];

  constructor(private rxModalService: RxModalService) {
    super();
  }

  openPropertiesEditor(): void {
    this.rxModalService
      .openModal({
        title: `Edit cell display properties for ${this.columnName}`,
        data: {
          cellDisplayProperties: this.value ? cloneDeep(this.value) : [],
          isReadOnly: this.isDisabled,
          columns: this.columns
        } as ICellDisplayPropertiesEditorConfig,
        content: RecordGridCellDisplayPropertiesEditorComponent,
        size: OpenViewActionModalSize.Large as any,
        testID: 'edit-cell-display-properties'
      })
      .then((properties: ICellDisplayProperties[]) => {
        this.value = properties;
      })
      .catch(noop);
  }
}
